.card{
    margin-top: 10px;
    padding: 10px;
    /*add shadows to create the card effect*/
    -webkit-box-shadow: 0 4px 8px 0 rgb(0, 0, 0, 0.2);
            box-shadow: 0 4px 8px 0 rgb(0, 0, 0, 0.2);
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}
.card:hover{
    -webkit-box-shadow: 0 8px 16px 0 rgb(0, 0, 0, 0.2);
            box-shadow: 0 8px 16px 0 rgb(0, 0, 0, 0.2);
}
.container{
padding: 2px 16px;
}
.btn{
    background-color: white;
    border: none;
    color: black;
    padding: 10px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 1.2em;
    font-family: cursive;
    border: 2px solid blue;
    border-radius: 25px;
    -webkit-transition-duration: 1s;
         -o-transition-duration: 1s;
            transition-duration: 1s;
    -webkit-box-shadow: 0 8px 16px 0 rgb(0, 0, 0, 0.2),0 6px 20px 0 rgb(0, 0, 0, 0.19);
            box-shadow: 0 8px 16px 0 rgb(0, 0, 0, 0.2),0 6px 20px 0 rgb(0, 0, 0, 0.19);
}
.btn:hover{
    background-color: blue;
}

#title{
    font-size: 4em;
    background: -webkit-gradient(linear, left top, right top, from(orange), color-stop(yellow), color-stop(green), color-stop(cyan), color-stop(blue), to(violet));
    background: -webkit-linear-gradient(left, orange, yellow, green, cyan, blue, violet);
    background: -o-linear-gradient(left, orange, yellow, green, cyan, blue, violet);
    background: linear-gradient(to right, orange, yellow, green, cyan, blue, violet);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
input {
    width: 180px;
    padding: 0px 50px;
    margin-right: 10px;
    text-align: center;
    font-family: cursive;
    font-size: 15px;
    border-radius: 25px;
}
input:focus{
    outline-color: aliceblue;
}

#res{
    font-family: cursive;
}
#error{
    width: 200px;
    color:red;
    font-family: cursive;
}
form{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

@media only screen and (max-width: 870px){
    #title{
        font-size: 3em;
    }
    form{
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }
    input{
        height: 36px;
        margin-bottom:-13px;
    }
}


